<template lang="pug">
    v-card 
        v-toolbar
            v-toolbar-title 波段策略管理
            v-spacer
            v-btn(icon @click="refresh")
                v-icon mdi-refresh

        v-list
            dtmrunstatuslistitem(
                v-for="ex in exConfigs"
                :key="ex.filename"
                :fileName="ex.filename"
                :buymoney="ex.buymoney"
                :runminutes="ex.runminutes"
                :startprice="ex.startprice"
                :profit="ex.currencyprofit"
                :sepminutes="ex.sepminutes"
                :status="ex.RunStatus"
                :donecycle="ex.donecycle"
                :cyclechange="ex.cyclechange"
                :maxmoneyonecycle="ex.maxmoneyonecycle"
                :onehandmoney="ex.onehandmoney"
                :volumetimes="ex.currentvolumetimes"
            )
            

</template>

<script>
import * as api from "@/apis/wswebapi"
import { get} from 'vuex-pathify'
export default {
    data: () => ({
    exConfigs: [],
    ExName:"WKJ"
    }),
    computed:{
        isLogin:get('user/islogin'),
        exbotisexpire:get('user/exbotisexpire'),
    },
    methods:{
        refresh(){
            api.DTMGetAll().then(res=>{
                if (res.code==200){
                    this.exConfigs=res.data;
                    console.log(res.data)
                }
                
            })
        }
    },

    components: {
      dtmrunstatuslistitem: () => import('@/views/dtm/dtmComponents/runstatuslistitem'),
    },
}
</script>